.goodsMenu {
    display: grid;
    grid-template-rows: repeat(5,130px);
    grid-gap: 10px;
    padding-top: 5px;
}

.goodsMenu .item {
    justify-self: center;
}

.goodsMenu .text {
    align-self: center;
text-transform: uppercase;
    margin-bottom: 5px;
}

.goodsMenu .buttons {
    justify-self: center;
    padding-top: 10px;

}